.DebuggerButton {
  text-align: center;
  margin-top: 40px;
}

.DebuggerButton-Icon {
  font-size: 46px;
  padding: 10px;
}

.DebuggerButton-Icon:hover {
  background-color: #c4f0ff;
  /* border: 3px solid #c23d4b; */
  border-radius: 100%;
  cursor: pointer;
  display: inline-block;
  font-size: 46px;
}

.DebuggerButton-Issues {
  background-color: white;
  box-shadow: rgba(50, 50, 93, 0.5) 0px 15px 35px,
    rgba(0, 0, 0, 0.07) 0px 5px 15px;
  font-size: 36px;
  padding: 15px;
  position: fixed;
  bottom: 40px;
  right: 0px;
  z-index: 1000;
  width: 100vw;
}

.DebuggerButton-Issue {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.DebuggerButton-Issue + .DebuggerButton-Issue {
  margin-top: 10px;
}

.DebuggerButton-Issue-Icon {
  box-shadow: rgba(50, 50, 93, 0.5) 0px 15px 35px,
    rgba(0, 0, 0, 0.07) 0px 5px 15px;
  padding: 5px 10px;
}

.DebuggerButton-Issue-Text {
  margin-left: 10px;
}
