.DebuggingDemo {
  background-color: #ffe0f5;
  display: flex;
  padding: 20px;
}

.DebuggingDemo-Left {
  background-color: #c85d42;
  flex: 1;
  padding: 20px;
}

.DebuggingDemo-Inputs {
  background-color: #9cdbff;
  font-size: 24px;
  padding: 20px;
}

.DebuggingDemo-Inputs p + p {
  margin-top: 20px;
}

.DebuggingDemo-Right {
  background-color: #159570;
  flex: 2;
  margin-left: 40px;
  padding: 20px;
}

.DebuggingDemo-Image {
  background-color: #3e2b6a;
  height: 350px;
}

.DebuggingDemo-Image + .DebuggingDemo-Image {
  margin-top: 20px;
}

.DebuggingDemo-no_offset .DebuggingDemo-Inputs {
  position: sticky;
}

.DebuggingDemo-same_height .DebuggingDemo-Left {
  position: sticky;
  top: 0px;
}

.DebuggingDemo-hidden_ancestor .DebuggingDemo-Inputs {
  position: sticky;
  top: 0px;
}

.DebuggingDemo-working .DebuggingDemo-Inputs {
  position: sticky;
  top: 0px;
}

.DebuggingDemo-unsupported_element {
}
