.Card {
  border: 2px solid #aab7c4;
  border-radius: 15px;
  box-shadow: rgba(50, 50, 93, 0.3) 0px 15px 35px,
    rgba(0, 0, 0, 0.2) 0px 5px 15px;
  padding: 10px;
  position: relative;
}

.Card + .Card {
  margin-top: 40px;
}

.Card img {
  display: block;
  max-width: 100%;
}

.Card-Small {
  width: 400px;
}

.Card-Inner {
  border-radius: 15px;
}

.Card-Inner p {
  padding: 15px 10px 10px 10px;
}

.Card-Inner p + img {
  margin-top: 10px;
}

.Card-Padded .Card-Inner {
  padding: 15px 10px;
}

.Card-Wash .Card-Inner {
  background-color: #f6f9fc;
}

.Card-Warning .Card-Inner {
  background-color: #fa755a;
  color: white;
}

.Card-Overflow-Hidden .Card-Inner {
  overflow: hidden;
}
